import { DeleteOutline } from "antd-mobile-icons";
import { brainStoreType, brainStoreUtil } from "../../../../assets/js/head/brainUtil";

function ChatRecord(props) {
    return (
        <div className="feature-item-row">
            <div className="feature-mid-item">
                <div className="chat-record-head">
                    <div className="chat-record-head-title"></div>
                    <div className="chat-record-head-operate flex-center" onClick={() => {
                        if (window.confirm('')) {
                            brainStoreUtil.delAll(brainStoreType.CHAT_LIST);
                            props.loadChatRecords();
                        }
                    }}><DeleteOutline /></div>
                </div>
                <div style={{ display: props.chatRecords.length !== 0 ? undefined : 'none' }} className="chat-record-list">
                    {
                        props.chatRecords.map((record, index) => {
                            return (
                                <div style={{ display: "flex" }} className="chat-record-list-item flex-center">
                                    <div key={index} className="chat-record-list-content flex-center" onClick={() => {
                                        props.showChatRecord(record);
                                    }}>
                                        {record.content}
                                    </div>
                                    <DeleteOutline className="chat-record-list-content-del" onClick={() => {
                                        if (window.confirm('' + record.content + '')) {
                                            brainStoreUtil.delById(brainStoreType.CHAT_LIST, record.dataId);
                                            props.loadChatRecords();
                                        }
                                    }} />
                                </div>
                            );
                        })
                    }
                </div>
                <div style={{ display: props.chatRecords.length === 0 ? undefined : 'none' }} className="chat-record-list">
                    <div className="sys-empty"></div>
                </div>
            </div>
        </div>
    );
}
export default ChatRecord;